import React,{useState}  from 'react';
import './css/Header.css';
import {NavLink} from 'react-router-dom';

const Header = () => {

    const [show, setShow] = useState(false)

 const handled = () => {
    setShow(show => !show)
 }
    
    return (
        <>
            <header className={`${show ? 'openMenu' : ' '}`}>
             
                <div className="container">
                    <div className="row">
                        <div className="col-3 ">
                            <div className="logo">
                                <NavLink exact to="/">Mighty.</NavLink>
                            </div>
                        </div>
                        <div className="col-9  text-right">
                        <button className="menu-trigger" onClick={ handled }>
                            <span></span>
                            <span></span>
                            <span></span>
                        </button>
                            <nav className="navbar">
                            <button className="close-trigger" onClick={ handled }>
                            <span></span>
                            <span></span>
                        </button>
                                <ul className="navbar-nav">
                                    <li className="nav-item"><NavLink exact to="/" className="nav-link">Home</NavLink></li>
                                    <li className="nav-item"><NavLink to="/About" className="nav-link">About</NavLink></li>
                                    <li className="nav-item"><NavLink to="/Work" className="nav-link">Work</NavLink></li>
                                    <li className="nav-item"><NavLink to="/Journal" className="nav-link">Journal</NavLink></li>
                                    <li className="nav-item"><NavLink to="/Contact" className="nav-link">Contact</NavLink></li>
                                </ul>
                            </nav>
                        </div>
                    </div>
                </div>
            </header>
        </>

    )
}

export default Header;